export default {
	data() {
		return {
			codeTime:0,
			mydata:{
				phone:'',
				checkNum:''
			},
			sessionId:''
		}
	},
	onLoad(option){
		this.sessionId = option.sessionId
		console.log(option)
	},
	methods: {
		// 获取验证码
			getCheckNum(){
				if(this.codeTime>0){
					uni.showToast({
						title: '不能重复获取',
						icon:"none"
					});
					return;
				}
				// 验证手机号合法性
				var mPattern = /^1[34578]\d{9}$/;
				if(mPattern.test(this.mydata.phone) == false){
					uni.showToast({
						title: '请输入正确的手机号',
						icon:"none"
					})
				}else{
					// 请求服务器，发送验证码到手机发送成功，开启倒计时
					this.codeTime = 60
					uni.request({
						url:'https://www.nssnail.com/wx/validatePhoneNumber',
						method:'POST',
						data:{
							sessionId:this.sessionId,
							phoneNumbers:this.mydata.phone
						},
						success: (res) => {
							if(res.data.stat == 1){
								uni.showToast({
									title:res.data.message,
									icon:'success',
									duration:2000
								})
							}else if(res.data.stat == 3){
								uni.showModal({
								    title: '提示',
								    content: '登录过期，请重新登录',
								    success: function (res) {
								        if (res.confirm) {
								            uni.navigateTo({
								            	url:'../myInit/myInit'
								            })
								        }
								    }
								});
							}else if(res.data.stat == 0){
								uni.showToast({
									title:res.data.message,
									icon:'none'
								})
							}
						}
					})
					let timer = setInterval(()=>{
						this.codeTime--;
						if(this.codeTime<1){
							clearInterval(timer);
							this.codeTime = 0
						}
					},1000)
				}
			},
			
			// 提交
			sure(){
				if(this.mydata.phone != '' && this.mydata.checkNum != ''){
					uni.request({
						url:'https://www.nssnail.com/wx/judgeCode',
						method:'POST',
						data:{
							code:this.mydata.checkNum,
							sessionId:this.sessionId
						},
						success:(res)=>{
							if(res.data.stat == 1){
								uni.showToast({
									title:'修改手机号成功',
									icon:'success',
									duration:2000,
									success: (res) => {
										setTimeout(function(){
											uni.navigateTo({
												url:'../information/information'
											})
										},2000);
									}
								})
							}else if(res.data.stat == 3){
									uni.showModal({
									    title: '提示',
									    content: '登录过期，请重新登录',
									    success: function (res) {
									        if (res.confirm) {
									            uni.navigateTo({
									            	url:'../myInit/myInit'
									            })
									        }
									    }
									});
								}
						}
					})
				}else{
					uni.showToast({
						title:'请补全信息',
						icon:'none'
					})
				}
			}
		}
}
